/* Change color cancel botton */ 
.c-btn__cancel {
    padding: 8px 16px;
    background-color: #f44336;
}

/* Span password and information position */ 
.c-information {
    float: right;
    margin: 8px 8px 8px 0;
}


/* Position it in the top right corner outside of the modal */
.c-close {
    position: absolute;
    right: 16px;
    top: 0;
    color: black;
    font-size: 35px;
    font-weight: bold;
    cursor: pointer;
}