/* Avatar image */
img {
    width: 40%;
    border-radius: 50%;
    border: 1px solid black;
}

/* Bordered form */
form {
    border: 3px solid black;
}

/* Horizontal line to separated the lines */
hr {
    border: 2px solid #f1f1f1;
}

label {
    margin: auto auto;
    padding: 0 8px;
}

a {
    color: dodgerblue;
}

/* Full-width inputs */
input[type=text], input[type=password] {
    width: 100%;
    padding: 8px 8px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid black;
    box-sizing: border-box;
}

/* Full-width input fields */
button {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 8px 8px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    & :hover {
        opacity: 0.8;
    }
}

/* Margin adjust input*/
input[type=checkbox] {
    margin: 8px 0;
}

